import React from 'react'

export const ConfirmDelete = ({ setDeleteNote, onConfirmDelete }) => {
    return (
        <div className='confirmDelete'>
            <div className="deleteBox">
                <p>Are you sure to Delete Note?</p>
                <div className="btn">
                    <button onClick={onConfirmDelete}>Yes</button>
                    <button onClick={onConfirmDelete}>No</button>
                </div>
            </div>
        </div>
    )
}
